import React,{ useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {_getRoomsDetail} from "../../../../redux/actions";
import {useParams} from "react-router-dom";

const RoomsDetail = (props)=> {
    let { _getRoomsDetail } = props
    let { id } = useParams()

    useEffect(()=> {
        if(id){
            _getRoomsDetail(id,{
                type:"update"
            })
        }

    },[id])

    return (
        <div className="w-full">
            <h1>Rooms Detail</h1>
        </div>
    )
}

RoomsDetail.propTypes = {
    _getRoomsDetail: PropTypes.func.isRequired
}
RoomsDetail.defaultProps = {}

export default connect(()=> {
    return {}
},{_getRoomsDetail})(React.memo(RoomsDetail))
